  /* App Styles */
  .App {
    text-align: center;
    margin-top: 50px;
  }
  
  .button-container {
    margin-top: 10px;
  }
  
  button.theme-button {
    padding: 15px 25px;
    font-size: 1.2em;
    cursor: pointer;
    border: 2px solid #fff;
    border-radius: 8px;
    background: linear-gradient(45deg, #4CAF50, #2196F3);
    color: #fff;
    transition: all 0.3s ease-in-out;
  }
  
  button.theme-button:hover {
    transform: scale(1.1);
    background: linear-gradient(45deg, #2196F3, #4CAF50);
    border-color: #2196F3;
  }
  
  